<template>
  <div id="myFollow">
    <mt-header title="我的关注" fixed>
      <mt-button  icon="back" slot="left" @click="handleBack()">返回</mt-button>

    </mt-header>

    <div class="lise_40"></div>

    <section class="nav_tab">
      <i :class="searchKey.selectd == 1 ? 'selectd':'' " @click="setSelectd(1)">问题</i>
      <i :class="searchKey.selectd == 2 ? 'selectd':'' " @click="setSelectd(2)">专家</i>
    </section>
    <div class="lise_10"></div>
    <!-- 我的报 Warp -->
    <div id="follow_Warp">
      <!-- ****** 全部 ****** -->
      <div v-if="searchKey.selectd == 1">
        <li v-for="item in list">
          <router-link :to="{path: '/askDetail', query:{qid: item.item_id}}">
            <FollowList :itemData="item"></FollowList>
          </router-link>
       </li>
      </div>

      <div v-if="searchKey.selectd == 2">
        <li v-for="item in expertList">
          <router-link :to="{path: '/expert', query:{eid: item.item_id}}">
            <expertFollowCell :itemData="item"></expertFollowCell>
          </router-link>
       </li>
      </div>

    </div>


    <div class="noData" v-if="list.length == 0">数据为 0 条</div>
    <div @click="seeing">
      <div class="loading-start" v-if="loading"></div>
      <span class="more" v-if="!complete && !loading">点击加载更多</span>
    </div>
  </div>
</template>

<script>
import { Toast,Indicator,Loadmore,DatetimePicker} from 'mint-ui'
import {getStore, setStore} from '../../components/common/mUtils'
import FollowList from './children/FollowList'
import expertFollowCell from './children/expertFollowCell'
import $ from 'webpack-zepto'
export default {
  components:{
    FollowList,
    expertFollowCell
  },
  data(){
    return{
      userID:'',
      userToken:'',
      list:[],
      expertList:[],
      complete: false, // 数据是否加载完成
      loading: false, // 是否在请求中
      curPage:1, // 当前页
      pageSize:10, // 默认显示10数据
      loading:false,
      complete:true,
      searchKey:{
        page:1,
        selectd:1,
        complete:false,
      },
    }
  },
  created(){
    this.userToken = getStore('userToken')
    this.userID = getStore("userID")
  },
  mounted(){
    this.getData()

    this.getExpertData()
  },
  filters:{
  },
  methods: {
    handleBack(){
      this.$router.replace('/myinfo');
    },
    gotoAddress(path){
      this.$router.push(path)
    },
    setSelectd(idx){
      this.searchKey.selectd = idx
    },
    getData(){
      Indicator.open();
      let pStr = '?curPage='+this.searchKey.page+'&pageSize='+this.pageSize+'&token='+this.userToken
      let getUrl = this.CFWConfig.APIHost + '/v3/ask/mygzwenti'+pStr
      // console.log(getUrl)
      this.$http.get(getUrl).then(response => {
        let code = response.data.code
        if (code == 200) {
          let arr = response.data.result
          if (arr.length > 0) {
            // 如果小于10条就是加载完了
            this.complete = arr.length < 10 ? true :false
            arr.forEach((item) => this.list.push(item))
          } else {
            this.complete = true
            this.allLoaded = true
          }
        }
        this.searchKey.page++
        this.loading = false // 请求完成
        Indicator.close();
      }, response => {
        Toast('请求失败');
        console.log(response);
        Indicator.close();
      });
    },
    getExpertData(){
      let pStr = '?curPage='+this.searchKey.page+'&pageSize='+this.pageSize+'&token='+this.userToken
      let getUrl = this.CFWConfig.APIHost + '/v3/ask/mygzzj'+pStr
      // console.log(getUrl)
      this.$http.get(getUrl).then(response => {
        let code = response.data.code
        if (code == 200) {
          let arr = response.data.result
          if (arr.length > 0) {
            // 如果小于10条就是加载完了
            this.complete = arr.length < 10 ? true :false
            arr.forEach((item) => this.expertList.push(item))
          } else {
            this.complete = true
            this.allLoaded = true
          }
        }
        this.searchKey.page++
        this.loading = false // 请求完成
      }, response => {
        Toast('请求失败');
        console.log(response);
        Indicator.close();
      });
    },
    seeing(){

    }
  },
  watch:{
  }

}
</script>

<style>
/*引入公共样式*/
@import '../../style/common/style.css';

#myFollow{
  background-color: #f5f5f5;
}

#myFollow .noData{
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}

/***** 顶部的 nav_tab *****/
#myFollow .nav_tab{
  height: 35px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  background-color: #fff;
}

#myFollow .nav_tab i{
  font-style: normal;
  height: 35px;
  width: 98%;
  line-height: 35px;
  margin: 0 5px;
  /*border-bottom: 2px solid red;*/
}

#myFollow .nav_tab .selectd{
  border-bottom: 2px solid red;
}



#myFollow .header_more{
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
}

#myFollow .header_more .search{
  /*margin-right: 15px;*/
}


#report_Warp {
  margin-top:10px;
}

#report_Warp .range_time{
  background-color: #fff;
  height: 36px;
  display: flex;
  align-items: center;
  padding-left:10px;
  font-size: 13px;
}

#report_Warp .range_time i{
  color: #26a2ff;
  padding: 3px 6px;
  font-style: normal;
  font-size: 13px;
  line-height: 13px;
  margin-right: 10px;
  border: 1px solid #26a2ff;
}

#report_Warp .range_time em{
  background-color: #ffc107;
  font-size: 13px;
  font-style: normal;
  padding: 3px 5px;
  color: #fff;
  border-radius: 2px;
}

#report_Warp .ML{
  margin-left: 10px;
}



.loginBtnTop{
  border-top: 1px solid #EEE;
  height: 30px;
}

.loading-start {
  margin: 2px auto 20px auto;
  position: relative;
  animation: rotate-forever 1s infinite linear;
  height: 30px;
  width: 30px;
  border: 4px solid #80bd01;
  border-right-color: transparent;
  border-radius: 50%;
}

.more{
  height: 40px;
  line-height: 40px;
}

/*弹出框*/
.calendar-dialog{
    position: absolute;
    left:0;
    top:0;
    right:0;
    bottom:0;
}

.calendar-dialog-mask{
    background:rgba(000,000,000,.8);
    width:100%;
    height:calc(100vh);
}

.calendar-dialog-body{
    background: #fff;
    position: absolute;
    left:50%;
    top:50%;
    transform: translate(-50%,-50%);
    padding:20px;
    border: 1px solid #eee;
    border-radius: 2px;
}

@keyframes rotate-forever {
  0% {
    transform: rotate(0deg)
  }
  100% {
    transform: rotate(360deg)
  }
}

</style>



